.submit-container {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 24px;

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;

    li {
      margin-bottom: 24px;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}