.get-uid-modal {
  display: flex;
  justify-content: center;
  align-items: center;

  &-container {
    width: 50%;
    height: 70%;
    min-width: 320px;
    max-width: 500px;
    min-height: 450px;
    max-height: 600px;
    box-sizing: border-box;
    border-radius: 8px;
    background-color: #fff;
    overflow: hidden;
    outline: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .get-uid-modal-view {
    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;

    &-image {
      display: flex;
      justify-content: center;
      align-items: center;
      height: calc(100% - 60px);
      width: 100%;
      flex: 1;
      box-sizing: border-box;
      padding-top: 24px;

      >img {
        height: 100%;
      }
    }

    &-buttons {
      height: 60px;
      box-sizing: border-box;
      padding: 12px 24px;
      display: flex;
      justify-content: flex-end;

      >button {
        margin-left: 12px;
      }
    }
  }
}

@media screen and (max-width: 880px) {
  .get-uid-modal-stepper {
    display: none !important;
  }
}