.learn-more-modal {
  display: flex;
  justify-content: center;
  align-items: center;

  &-container {
    width: 50%;
    height: 70%;
    min-width: 300px;
    max-width: 450px;
    min-height: 450px;
    max-height: 600px;
    box-sizing: border-box;
    border-radius: 8px;
    background-color: #fff;
    overflow: hidden;
    outline: none;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }
}
