.box-main {
  width: 70%;
  height: 65%;
  min-width: 250px;
  max-width: 400px;
  min-height: 420px;
  max-height: 550px;
  box-sizing: border-box;
  border-radius: 8px;
  background-color: #fff;
  overflow: hidden;
  position: relative;
}

.main-footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  color: #ddd;
  font-size: 12px;
  text-align: center;
  padding-bottom: 8px;

  span {
    display: inline-block;
    margin-left: 8px;
    cursor: pointer;
    transition: color .3s ease-in-out;

    &:hover {
      text-decoration: underline;
      color: #aaa;
    }
  }
}

.container {
  width: 100%;
  height: 100%;

  .react-swipeable-view-container {
    height: calc(100% - 48px);
  }
}

.count-selection {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}